main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 5rem;
}

#top {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  width: 600px;
}

#buttons {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  width: 600px;
}

button {
  width: 150px;
  font-size: 5rem;
  padding: 1rem 2rem;
  margin: 0;
  text-decoration: none;
}

#clear {
  flex-basis: 600px;
}

@media only screen and (max-device-width: 812px) and (-webkit-min-device-pixel-ratio: 3) and (orientation: portrait) {
  /* Responsive */
  main {
    font-size: 3rem;
  }

  #top {
    width: 350px;
  }

  #buttons {
    width: 350px;
  }

  button {
    width: 87px;
    font-size: 2rem;
  }
}
